import React, { useState } from 'react';
import styled from 'styled-components';

const SimuladorContainer = styled.div`
  font-family: 'Montserrat', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background: linear-gradient(180deg, #011E3B, #ffffff);
  color: #011E3B;
  padding: 20px;
  text-align: center;
`;

const Input = styled.input`
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
  width: 80%;
  max-width: 400px;
  border: 1px solid #ddd;
  border-radius: 8px;
`;

const Button = styled.button`
  margin-top: 20px;
  padding: 10px 20px;
  background: linear-gradient(90deg, #ddba33, #b5972d);
  color: white;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;

  &:hover {
    transform: scale(1.05);
    background: linear-gradient(90deg, #b5972d, #ddba33);
  }
`;

const Resultado = styled.div`
  margin-top: 20px;
  font-size: 18px;
  font-weight: bold;
`;

const SimuladorInvestimento = () => {
  const [valor, setValor] = useState('');
  const [rentabilidade, setRentabilidade] = useState('');
  const [total, setTotal] = useState(null);

  const calcularRentabilidade = () => {
    const valorNumerico = parseFloat(valor);
    const rentabilidadeNumerica = parseFloat(rentabilidade);

    if (isNaN(valorNumerico) || isNaN(rentabilidadeNumerica)) {
      alert('Por favor, insira valores válidos para o cálculo.');
      return;
    }

    const resultado = valorNumerico + valorNumerico * (rentabilidadeNumerica / 100);
    setTotal(resultado.toFixed(2)); // Formata o valor para duas casas decimais
  };

  return (
    <SimuladorContainer>
      <h1>Simulador de Investimentos</h1>
      <Input
        type="number"
        value={valor}
        onChange={(e) => setValor(e.target.value)}
        placeholder="Digite o valor a investir"
      />
      <Input
        type="number"
        value={rentabilidade}
        onChange={(e) => setRentabilidade(e.target.value)}
        placeholder="Digite a taxa de juros (%)"
      />
      <Button onClick={calcularRentabilidade}>Calcular Rentabilidade</Button>
      {total && <Resultado>Total após rentabilidade: R$ {total}</Resultado>}
    </SimuladorContainer>
  );
};

export default SimuladorInvestimento;

